import {Session} from './../../../services/session';
import {WidgetProvider} from './../../../providers/widget-provider';
import {Webpage} from './../../../models/webpage';
import {TranslateService} from '@ngx-translate/core';
import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';

import {IDLRecruitingProvider} from './../../../providers/idl-recruiting-provider';

/**
 * Generated class for the IdlRecruitmentPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-idl-recruitment',
  templateUrl: 'idl-recruitment.html',
})
export class IdlRecruitmentPage {


  postItem: any;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public iDLRecruitingProvider: IDLRecruitingProvider,
              public translateService: TranslateService,
              public widgetProvider: WidgetProvider,
              public session: Session) {
    this.postItem = {}
    const item = this.navParams.data;
    this.iDLRecruitingProvider.PostDetail(item.id).subscribe(res => {
      if (res.errcode == 0) {
        this.postItem = res.result;

        if (!this.postItem.applyUrl) {
          this.postItem.applyUrl = `http://fishnet.fih-foxconn.com/Job/JobApply?ChannelCode=C01&jobId=1&LanguageCode=zh-TW&AccountId=${this.session.userId}`
        } else {
          this.postItem.applyUrl += this.session.userId;
        }
      }
    })

  }


  ionViewDidLoad() { 
  }

  applyPost() {
    let page: Webpage = {
      url: this.postItem.applyUrl,  // 链接地址
      title: this.translateService.instant('HOME_ENGINEER_RECRUITMENT'),  // 标题
      description: "",   // 摘要内容
      imageUrl: "",   // 图片路径
      appId: "" ,// 應用ID
      options:{}
    };
    this.widgetProvider.openWebpage(page, this.navCtrl);

  }

}



